.tournament-container{
  padding: 0 15px 15px 15px;
}

.tournament-section{
  margin-bottom: 30px;
}

.tournament-section:last-child {
  margin-bottom: 0px;
}

.section-header{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.section-header__title{
  margin: 0;
  font-size: 20px;
  font-weight: 500;
}

.tournament__components{
  margin: 10px 0 0 0;
}