.roundrobin-grid__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 0 20px 0;
}

.roundrobin-grid__title {
  color: #3A3A3A;
  font-size: 20px;
  margin: 0;
  font-weight: 500;
}

.roundrobin-grid__wrapper {
  margin: 0 0 40px 0;
}

.roundrobin-grid__sheet {
  overflow: auto;
  overflow-y: hidden;
  width: 100%;
  display: flex;
}
.roundrobin-grid__sheet::-webkit-scrollbar {
  width: 0;
}

.roundrobin-grid__content {
  display: table;
  border-collapse: collapse;
  width: max-content;
}

.roundrobin-grid__content--sticky{
  display: table;
  border-collapse: collapse;
  position: sticky;
  left: 0;
  z-index: 9;
  background-color: #fff;
  overflow: auto;
  overflow-y: hidden;
  white-space: nowrap;
}

.roundrobin-grid__row {
  display: table-row;
  height: 62px;
}

.roundrobin-grid__header {
  color: #3A3A3A;
  font-size: 16px;
  font-weight: 500;
  padding: 10px;
  display: table-cell;
  border: 1px solid #CCCCCC;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
}

.roundrobin-grid__header--inner{
  font-size: 12px;
}

.roundrobin-grid__cell{
  display: table-cell;
  border: 1px solid #CCCCCC;
  color: #3A3A3A;
  font-size: 16px;
  font-weight: 400;
  padding: 0px 8px;
  vertical-align: middle;
  white-space: nowrap;
  overflow: auto;
  height: 62px;
  max-width: 120px;
}

.roundrobin-grid__cell--double{
  display: table-cell;
  border: 1px solid #CCCCCC;
  vertical-align: middle;
  height: 100%;
  white-space: nowrap;
}

.roundrobin-grid__cell-container{
  display: flex;
  justify-content: space-between;
  white-space: nowrap;
  width: 100%;
}

.roundrobin-grid__cell--inner {
  border-right: 1px solid #CCCCCC;
  color: #3A3A3A;
  font-size: 17px;
  font-weight: 400;
  white-space: nowrap;
  height: 62px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  width: 65px;
}

.roundrobin-grid__cell--inner:last-child {
  border-right: none;
}

.roundrobin-grid__cell--grey {
  background-color: #F3F3F3;
}

.roundrobin-grid__cell > div {
  display: flex;
  justify-content: center;
  align-items: center;
  white-space: nowrap;
}

.roundrobin-grid__cell-participant{
  overflow: hidden;
  overflow-x: auto;
}

.olympic-grid__match-section-sets{
  display: flex;
  align-items: center;
  width: 100%;
}
.roundrobin-grid__cell-sets-container{
  display: flex;
  align-items: center;
}

.roundrobin-grid__cell-score{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  cursor: pointer;
}

.roundrobin-grid__cell-score p{
  display: flex;
  align-items: center;
  white-space: nowrap;
}

.roundrobin-grid__cell-score > p > span{
  display: flex;
  justify-content: center;
  color: #4694D9;
  white-space: nowrap;
}

.roundrobin-grid__cell-order{
  display: table-cell;
  border: 1px solid #CCCCCC;
  color: #3A3A3A;
  font-size: 17px;
  font-weight: 400;
  vertical-align: middle;
  text-align: center;
}