.match-group__container {
  margin: 0 0 20px 0;
}

.match-group__title {
  color: #3A3A3A;
  font-size: 20px;
  font-weight: 500;
}

.match-table__container {
  border: 1px solid #CCCCCC;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0 0 40px 0;
  white-space: nowrap;
  overflow: auto;
  overflow-y: hidden;
}

.match-table__body {
  border-bottom: 1px solid #CCCCCC;
  display: flex;
  align-items: center;
  width: 100%;
  white-space: nowrap;
}

.match-table__content-names {
  padding: 10px;
  color: #3A3A3A;
  font-size: 19px;
  font-weight: 400;
  overflow: hidden;
  min-width: 150px;
}

.match-table__content-names > p {
  padding: 0 0 10px 0;
  margin: 0 0 10px 0;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.match-table__content > p > span {
  color: #4694D9;
}
.match-table__content-names > p:last-child {
  border: 0;
  padding: 0;
  margin: 0;
}

@media screen and (max-width: 420px) {
  .match-table__content-names {
    width: 150px;
  }
}

.match-table__content-sets{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 105px;
}

.match-table__content-sets-score {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  padding: 0 0 10px 0;
  margin: 0 5px 10px 0;
}
.match-table__content-sets-score:last-child {
  border: 0;
  padding: 0;
}
.match-table__content-sets-score > p {
  margin: 0 5px 0 0;
  cursor: pointer;
  min-width: 30px;
  text-align: center;
}
.match-table__content-sets-score > p:last-child {
  margin: 0;
}
.match-table__content-sets-score > p > span {
  color: #4694D9;
}

.match-table__content-order{
  padding: 10px;
  text-align: center;
  color: #3A3A3A;
  font-size: 19px;
  font-weight: 400;
}

.match-table__content-ball{
  display: flex;
  align-items: center;
  color: #3A3A3A;
  font-size: 19px;
  margin: 0 0 0 auto;
  font-weight: 400;
  width: 30px;
}