.noplayers-container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	height: 100%;
	width: 100%;

	.rockets-icon {
		fill: var(--palette-live-grey-6);
	}

	.noplayers-title{
		font-weight: 600;
		font-size: 24px;
		text-align: center;
		margin: 30px 0 15px 0;
		color: var(--palette-live-grey-0);
	}

	.noplayers-text {
		font-weight: 400;
		font-size: 18px;
		text-align: center;
		max-width: 185px;
		margin: 0;
		color: var(--palette-live-grey-0);
	}
}

.search__container {
	margin: 0 20px 0 20px;
}

.players__block {
	height: 100%;
	margin: 20px 20px 0 20px;
	overflow: auto;

	.players__name-container {
		display: flex;
		align-items: center;
		margin-right: 5px;

		.players__number {
			font-size: 20px;
			margin: 0 15px 0 0;
		}
	}
}