.olympicplus-grid__container {
  overflow: auto;
  margin-bottom: 20px;
}

.olympicplus-grid__title {
  font-size: 20px;
  font-weight: 500;
  margin: 10px 0;
}

.olympicplus-grid__column {
  border-top: 1px solid #ccc;
  border-right: 1px solid #ccc;
}

.olympicplus-grid__column:first-child {
  border-left: 1px solid #ccc;
}

.olympicplus-grid__column:last-child {
  border-bottom: 1px solid #ccc;
}

.olympicplus-grid__column-title {
  margin: 0;
  padding: 5px;
  font-weight: 600;
  text-align: center;
  border-bottom: 1px solid #ccc;
  font-size: 17px;
}

.olympic-grid__match-section-participant,
.olympic-grid__match-section-sets {
  display: flex;
  align-items: center;
}

.olympic-grid__match-section-sets{
  margin-left: 10px;
}

.olympic-grid__match-number {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 23px;
  font-size: 17px;
  margin: 0 10px 0 5px;
}

.olympic-grid__match-participant {
  display: flex;
  align-items: center;
  font-size: 17px;
  height: 4.5vh;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.olympic-grid__match-section-sets-set {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.olympic-grid__match-section-sets > div {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
}

.olympic-grid__match-section-sets-set-score{
  text-align: center;
  margin-left: 10px;
  color: rgb(54, 123, 207);
  font-size: 19px;
  width: 25px;
}

.olympic-grid__match-section-sets-set-tiebreak {
  font-size: 13px;
}

