.olympic-grid__result-order,
.olympic-grid__result-name {
  margin: 0 5px;
  font-size: 17px;
}

.olympic-grid__match-dobles {
  padding: 2.5px 0;
}

.olympic-grid__match-double-player {
  display: flex;
  align-items: center;
  font-size: 17px;
  height: 3vh;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.olympic-grid__match-noplayer {
  display: flex;
  align-items: center;
  font-size: 17px;
  height: 6vh;
}